.faucet {
  position: fixed;
  right: 0px;
  bottom: 0px;
  color: #fbfbfb;
  border: 2px solid #ecf1f9;
  padding: 6px;
  border-radius: 18px;
  cursor: pointer;
  z-index: 6;
  @media (max-width: 1024px) {
    position: absolute;
    left: auto;
    right: 30px !important;
    bottom: 45px !important;
    width: 134px;
    text-align: center;
  }
}

.faucet-list {
  position: fixed;
  right: 80px;
  bottom: 10px;
  cursor: pointer;
  z-index: 6;

  button {
    margin-left: 8px;
  }
  @media (max-width: 1024px) {
    position: absolute;
    left: auto;
    right: 35px !important;
    bottom: 100px !important;
    cursor: pointer;
    z-index: 6;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 134px;
    button {
      margin-left: 0px;
    }
  }
}

.faucet-button {
  font-family: "DM Sans", sans-serif;
  background: #566ffe;
  padding: 12px 20px;
  border-radius: 40px;
  color: #ecf1f9;
  font-size: 14px;
  font-weight: 500;
  border: none;
  cursor: pointer;

  a {
    text-decoration: none;
    color: #ecf1f9;
  }

  &.light {
    background: transparent;
    color: #fbfbfb;
    border: 2px solid #ecf1f9;
  }

  &.disabled {
    opacity: 0.5;
  }
}

[data-theme="dark"] .light {
  color: #ecf1f9;
}
